<template>
  <v-container>
    <v-text-field
      v-model="search"
      label="Escribe que musica quieres encontrar"
      :loading="loading"
      @keyup.enter="searchTracks()"
      outlined
    ></v-text-field>
    <v-btn color="primary" @click="searchTracks()" :loading="loading"
      >Buscar</v-btn
    >
    <v-row style="margin-top: 15px">
      <v-col v-for="t in tracks" :key="t.id" md="4" sm="12">
        <Caratula :track="t" />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Caratula from "@/components/Caratula.vue";
export default {
  name: "Home",

  components: { Caratula },
  data() {
    return {
      search: "",
      tracks: [],
      loading: false,
    };
  },
  methods: {
    async searchTracks() {
      this.loading = true;
      try {
        let result = await axios.get(
          "https://platzi-music-api.herokuapp.com/search",
          {
            params: { q: this.search, type: "track" },
          }
        );
        this.loading = false;
        this.tracks = result.data.tracks.items;
      } catch (error) {}
    },
  },
};
</script>
